/* ============================backBtn============================= */

.backBtn {
  position: fixed;
  top: 9%;
  left: 2%;
  padding: 15px;
  border-radius: 15px;
    transition: 0.5s ease-in-out;
}

.backBtn:hover {
  cursor: pointer;
  background-color: #cfcece;
  box-shadow: 5px 5px #000;
}
